<template>
  <Row :gutter="18" type="flex" justify="center" align="top">
    <Col span="4">
    </Col>
    <Col span="16">
      <div v-if="dataIsEmpty">
        <Col span="24" class="card"  v-for="(data,index) in datas" :key="index">
          <Card>
            <div>
              <h3 style="display: inline-block;">{{data.title}}</h3>
            </div>
            <div class="card-body">
              <img v-lazy="finalImg(data.title)">
            </div>
          </Card>
        </Col>
      </div>
      <div class="card" v-else>
        <Col span="24">
          <Card>
            <div class="card-title">
              <img v-lazy="defaultImgUrl">
            </div>
            <div class="card-body">
              当前模块无数据
            </div>
          </Card>
        </Col>
      </div>
    </Col>
    <Col span="4"></Col>
  </Row>
</template>

<script>
import Data from '../../../static/mork/blogs.json'

export default {
  name: 'MyBlogs',
  data () {
    return {
      datas: [],
      defaultImgUrl: '@/assets/image/loading.gif'
    }
  },
  created () {
    this.datas = Data
  },
  computed: {
    dataIsEmpty: function () {
      return this.datas.length !== 0
    }
  },
  methods: {
    finalImg: function (title) {
      return `../../static/mork/works/${title}/logo.png`
    }
  }
}
</script>

<style scoped>
  .card-title{
    width: 20%;
    height: 0;
    margin-bottom: 20%;
    display:inline-block;
  }
  .card-title img{
    width:100%;
    height: 100vm;
  }
  .card-body{
    float: right;
    width: 70%;
  }
  .card{
    padding:20px;
  }
</style>



